<template>
  <div class="content-container" v-on:click="toggleCard">
    <div class="player-card">
      <div v-if="!isFlipped">
        <img v-bind:src="myimage"/>
        {{isFlipped}}
      </div>
      <div v-else>
        Stats here
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'player-card',
  data () {
    return {
      isFlipped: false
    }
  },
  props: [
    'item'
  ],
  computed: {
    myimage: function () {
      return require(`../../assets/venue/players/${this.item.image}`)
    }
  },
  methods: {
    toggleCard () {
      if (this.isFlipped) {
        this.isFlipped = false
      } else {
        this.isFlipped = true
      }
    }
  }
}
</script>

<style>
  .player-card {
    margin: 3%;
    width: 332px;
    height: 528px;
    background-color: rgba(0,0,0, .75);
  }

  .player-card-head {
    height: 35px;
    background-color: #f00;
  }
  
  .player-card-head {
    bottom: 0;
    width: 100%;
    height: 35px;
    background-color: #f00;
  }
</style>



